.tiptap-dropdown-menu{--tt-dropdown-menu-bg-color: var(--white);--tt-dropdown-menu-border-color: var(--tt-gray-light-a-100);--tt-dropdown-menu-text-color: var(--tt-gray-light-a-600)}.dark .tiptap-dropdown-menu{--tt-dropdown-menu-border-color: var(--tt-gray-dark-a-50);--tt-dropdown-menu-bg-color: var(--tt-gray-dark-50);--tt-dropdown-menu-text-color: var(--tt-gray-dark-a-600)}.tiptap-dropdown-menu{z-index:50;outline:none;transform-origin:var(--radix-dropdown-menu-content-transform-origin);max-height:var(--radix-dropdown-menu-content-available-height)}.tiptap-dropdown-menu>*{max-height:var(--radix-dropdown-menu-content-available-height)}.tiptap-dropdown-menu[data-state=open]{animation:fadeIn 150ms cubic-bezier(0.16,1,0.3,1),zoomIn 150ms cubic-bezier(0.16,1,0.3,1)}.tiptap-dropdown-menu[data-state=closed]{animation:fadeOut 150ms cubic-bezier(0.16,1,0.3,1),zoomOut 150ms cubic-bezier(0.16,1,0.3,1)}.tiptap-dropdown-menu[data-side=top],.tiptap-dropdown-menu[data-side=top-start],.tiptap-dropdown-menu[data-side=top-end]{animation:slideFromBottom 150ms cubic-bezier(0.16,1,0.3,1)}.tiptap-dropdown-menu[data-side=right],.tiptap-dropdown-menu[data-side=right-start],.tiptap-dropdown-menu[data-side=right-end]{animation:slideFromLeft 150ms cubic-bezier(0.16,1,0.3,1)}.tiptap-dropdown-menu[data-side=bottom],.tiptap-dropdown-menu[data-side=bottom-start],.tiptap-dropdown-menu[data-side=bottom-end]{animation:slideFromTop 150ms cubic-bezier(0.16,1,0.3,1)}.tiptap-dropdown-menu[data-side=left],.tiptap-dropdown-menu[data-side=left-start],.tiptap-dropdown-menu[data-side=left-end]{animation:slideFromRight 150ms cubic-bezier(0.16,1,0.3,1)}