.tiptap-popover{--tt-popover-bg-color: var(--white);--tt-popover-border-color: var(--tt-gray-light-a-100);--tt-popover-text-color: var(--tt-gray-light-a-600)}.dark .tiptap-popover{--tt-popover-border-color: var(--tt-gray-dark-a-50);--tt-popover-bg-color: var(--tt-gray-dark-50);--tt-popover-text-color: var(--tt-gray-dark-a-600)}.tiptap-popover{z-index:50;outline:none;transform-origin:var(--radix-popover-content-transform-origin);max-height:var(--radix-popover-content-available-height)}.tiptap-popover>*{max-height:var(--radix-popover-content-available-height)}.tiptap-popover[data-state=open]{animation:fadeIn 150ms cubic-bezier(0.16,1,0.3,1),zoomIn 150ms cubic-bezier(0.16,1,0.3,1)}.tiptap-popover[data-state=closed]{animation:fadeOut 150ms cubic-bezier(0.16,1,0.3,1),zoomOut 150ms cubic-bezier(0.16,1,0.3,1)}.tiptap-popover[data-side=top],.tiptap-popover[data-side=top-start],.tiptap-popover[data-side=top-end]{animation:slideFromBottom 150ms cubic-bezier(0.16,1,0.3,1)}.tiptap-popover[data-side=right],.tiptap-popover[data-side=right-start],.tiptap-popover[data-side=right-end]{animation:slideFromLeft 150ms cubic-bezier(0.16,1,0.3,1)}.tiptap-popover[data-side=bottom],.tiptap-popover[data-side=bottom-start],.tiptap-popover[data-side=bottom-end]{animation:slideFromTop 150ms cubic-bezier(0.16,1,0.3,1)}.tiptap-popover[data-side=left],.tiptap-popover[data-side=left-start],.tiptap-popover[data-side=left-end]{animation:slideFromRight 150ms cubic-bezier(0.16,1,0.3,1)}